<template>
  <div>
    <select
      :placeholder="'select an option'"
      :value="actualValue"
      @change="handleChange"
    >
      <option
        v-for="optionValue in props.options"
        :value="optionValue"
        :key="optionValue"
      >
        {{ optionValue }}
      </option>
    </select>
    <span class="bottom"></span>
    <span class="right"></span>
    <span class="top"></span>
    <span class="left"></span>
  </div>
</template>

<script setup lang="ts">
import type { PropType } from "vue";

// Define component props
const props = defineProps({
  // Array of options to display in the select element
  options: {
    type: Array as PropType<string[]>,
    required: true,
  },
  // Currently selected option value
  actualValue: {
    required: true,
    type: String,
  },
  // Function to call when the selected option changes
  onChangeCallback: {
    type: Function,
    required: true,
  },
});

// Handle the change event of the select element
const handleChange = (event: Event) => {
  const target = event.target as HTMLSelectElement;
  props.onChangeCallback(target.value);
};
</script>

<style scoped>
div {
  position: relative;
  width: 100px;
}
/* Style for the select element */
select {
  background-color: var(--grey-dark3);
  height: 40px;
  border: solid 1px;
  color: var(--white);
  border: none;
  outline: none;
  width: 100px;
  height: 40px;
  border-radius: 5px;
  opacity: 1;
  text-align: right;
  border: 1px solid transparent;
  transition: background-color 0.3s ease-in-out;
}

select:focus {
  outline: none;
}

select::placeholder {
  color: hsla(0, 0%, 100%, 0.6);
}

span {
  position: absolute;
  background-color: var(--primary);
  transition: transform 0.5s ease;
}

.bottom,
.top {
  height: 1px;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.left,
.right {
  width: 1px;
  top: 0;
  bottom: 0;
  transform: scaleY(0);
}

.bottom {
  bottom: 0;
  transform-origin: bottom right;
}

select:focus ~ .bottom {
  transform-origin: bottom left;
  transform: scaleX(1);
}

.right {
  right: 0;
  transform-origin: top right;
}

select:focus ~ .right {
  transform-origin: bottom right;
  transform: scaleY(1);
}

.top {
  top: 0;
  transform-origin: top left;
}

select:focus ~ .top {
  transform-origin: top right;
  transform: scaleX(1);
}

.left {
  left: 0;
  transform-origin: bottom left;
}

select:focus ~ .left {
  transform-origin: top left;
  transform: scaleY(1);
}
</style>
